.flex {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
}
#home,
#basic-info,
#volunteer,
#conference,
#portfolio {
  padding: 50px 0;
  h1 {
    text-align: center;
    margin-bottom: 25px;
  }
}

.left {
  flex: 2 1 65%;
  padding: 0 25px 0 0;
  position: relative;
  top: 0;

  @media (max-width: 775px) { flex-basis: 100%; }

  h1.heading {
    margin: 0;
    padding: 0;
    font-size: 3em;
    line-height: .8em;
    font-family: $accent;
    text-align: left !important;

    @media (max-width: 500px) { font-size: 2.5em; }
    @media (max-width: 400px) { font-size: 2em; }

  }

  p {
    font-size: 1.1em;
    text-align: justify;
  }

}

.right {
  flex: 1 1 35%;
  background-size: cover;
  background-position: right center;
  border-radius: 5px;

  @media (max-width: 775px) {
    flex-basis: 100%;
    height: 400px;
    display: block;
    margin: 50px auto 0;
  }

}


img + p.small {
  width: 90%;
  display: block;
  margin: 10px auto;
  font-size: .8em;
  text-align: center;
}


#basic-info {
  ul li span {
    &.topic {
      font-weight: bold;
      display: block;
      i {
        width: 35px;
        padding: 5px 5px 5px 0;
      }
    }
    &.answer {
      font-style: italic;
        display: block;
        padding-left: 35px;
    }
  }
  p {
    display: block;
    width: 100%;
  }
  .image { padding-right: 15px; }

}

#volunteer {
  h2 {
    padding: 10px 0 0;
  }
  p {
    padding-top: 0;
    margin-top: 0;
  }
  .image { padding-left: 15px;}
}

#conference {
  h1 {
    margin-bottom: 0;
  }
  h2 {
    margin-bottom: 25px;
  }
  ul.big-events-list {
    li {
      display: inline-block;
      flex: 1 1 50%;
      padding: 10px 0;

      @media (max-width: 775px) { flex: 1 1 100%; }

      .title {
        display: inline-block;
        margin-bottom: 5px;
      }

      &.conference {
        &:before {
          font-family: FontAwesome;
          content: '\f0c0';
          width: 25px;
          display: inline-block;
        }
      }
      &.hackathon {
        &:before {
          font-family: FontAwesome;
          content: '\f121';
          width: 25px;
          display: inline-block;
        }
      }
      span {
        &.title {
          font-weight: bold;
        }
        &.date,
        &.location,
        &.extra {
          display: block;
        }
        &.date,
        &.location {
          padding-left: 30px;
        }
        .fa-trophy {
          color: $trophy;
          padding: 0 10px;
        }
      }
      /* Tooltip container */
      .tooltip {
        position: relative;
        display: inline-block;
      }

      /* Tooltip text */
      .tooltip .tooltiptext {
        visibility: hidden;
        width: 120px;
        background-color: $trophy;
        color: #000;
        text-align: center;
        padding: 5px 0;
        margin-left: 5px;
        border-radius: 6px;
        font-weight: normal;

        /* Position the tooltip text*/
        position: absolute;
        top: -5px;
        left: 105%;
        z-index: 1;
      }

      /* Show the tooltip text when you mouse over the tooltip container */
      .tooltip:hover .tooltiptext {
        visibility: visible;
      }

      .tooltip .tooltiptext::after {
        content: " ";
        position: absolute;
        top: 50%;
        right: 100%; /* To the left of the tooltip */
        margin-top: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: transparent $trophy transparent transparent;
      }
    }
  }
}



.image {
  width: 40%;
  @media (max-width: 775px) {
    width: 100%;
    order: 2;
    padding-right: 0;
    margin-top: 50px;
  }
}
.info {
  width: 60%;
  @media (max-width: 775px) { width: 100%; order: 1;}
}

